import React, { Fragment } from 'react';
import styled, { keyframes } from 'styled-components';

const grow = keyframes`
  0% {
    width: 0;
  }
  25% {
    width: 0;
  }
  100% {
    width: ${ ({ amount }) => amount}%;
  }
`;

const Bar = styled.div`
    background-color: #ff6c00;
    width: ${ ({ amount }) => amount}%;
    height: 4px;
    position: relative;
    animation: ${grow} 3s ease-in-out 1;
`;

const Icon = styled.div`
    width: 3em;
`;

const Rest = styled.div`
    background-color: #c3c3c3;
    width: 100%;
    height: 4px;
    margin-left: 1em;
`;


export default function Technologies({ name, amount }) {
    return (
        <div class="progress">
            <Icon>
                <i class={"fab fa-" + name}></i>
            </Icon>
            <Rest amount={100 - amount}>
                <Bar amount={amount} />
            </Rest>
        </div>
    )
}